import { FC } from 'react';

import {
  Wrapper,
  Inner,
  LeftBlock,
  RightBlock,
} from './FullWidthBarSmall.styles';
import { FullWidthBarSmallProps } from './FullWidthBarSmall.types';

/**
 * Currently used as the very top bar in the Masthead.
 *
 * It is a full-width bar with two blocks, one on the left and one on the right.
 *
 * The inner content is centred and never exceeds the width of the page
 */
const FullWidthBarSmall: FC<FullWidthBarSmallProps> = ({
  leftBlock,
  rightBlock,
}) => (
  <Wrapper>
    <Inner>
      <LeftBlock>{leftBlock}</LeftBlock>
      <RightBlock>{rightBlock}</RightBlock>
    </Inner>
  </Wrapper>
);

export default FullWidthBarSmall;
